*{
    margin:0;
}

.Achiev{
    width:100vw;
    height:450px;
    text-align: center;
    background-image:-webkit-linear-gradient(310deg,#AB46D2 7%,#A760FF 7% 93%,#AB46D2 93%);
    position: relative;
    display: flex;
    overflow: hidden;
}

.slide{
    min-width:100%;
    height:500px;
    transition:1s ease;
    position: relative;
    top:40px;
}
.btnleft,.btnright,.cbtnDiv{
    position: absolute;
    top:200px;
    border:none;
}
.btnleft{
    left:30px;
}
.btnright{
    right:30px;
}
.cbtnDiv{
    left: 50%;
    top:90%;
}
.cert,.achiev,.believ{
    color:#fff;
    text-shadow: 1px 1px #000,
    1px -1px #000,
    -1px -1px #000,
    -1px 1px #000,
    -5px 5px 10px rgb(100,100,100);
}

.cert h1,.achiev h1,.believ h1{
    width:400px;
    margin:auto;
    font-size: 4rem;
    border-bottom: 1px solid white;
    padding:2rem 2rem 7px;
}
.innerAc{
    margin:2rem;
}
.innerAc h3{
    position: relative;
    font-size: 1.5rem;
    margin:5px;
}

.innerAc h3::before{
    content: "oi1o";
    font-size: 10px;
    letter-spacing: 0;
    position: relative;
    right:30px;
    overflow: hidden;
    border-radius: 50%;
    margin-right: 5px;
    text-shadow: none;
    box-shadow: 1px 1px #000,
    1px -1px #000,
    -1px -1px #000,
    -1px 1px #000,
    -3px 3px 5px rgb(100,100,100);
    color:#ffff;
    background-color: #ffff;
}
.btnleft,.btnright{
    background-color: transparent;
    color:#fff;
    cursor: pointer;
}
.cbtn{
    border-radius: 50%;
    margin:5px;
    height:12px;
    width:10px;
    cursor: pointer;
    border:none;
    opacity:0.2;
    color:rgb(255, 255, 255);
}
.believ h1{
    border:1px solid white;
    width:500px;
    padding:50px;
    margin-top:5rem;
}