*{
    margin:0;
}
.MainSkills{
    width:100vw;
    height:900px;
    padding-top: 80px;
    background-color: #fff;
    background-image: url("https://www.transparenttextures.com/patterns/asfalt-dark.png");
    position: relative;
    z-index: 5;
    box-shadow: inset 0 0 5px rgb(100,100,100);
    display: inline-block;
    /* flex-wrap: wrap; */
    flex:50%;
}
.AboutMe{
    text-align: center;
    margin:auto;
    font-size: 4rem;
    color:#AB46D2;
    text-shadow: 1px 1px #fff,
                1px -1px #fff,
                -1px -1px #fff,
                -1px 1px #fff,
                -5px 0px 5px rgb(200,200,200),
                -5px 5px 5px rgb(100,100,100);
}

.topLine{
    height:1rem;
    width:90%;
    background-color: #AB46D2;
    box-shadow: 1px 1px #fff,
    1px -1px #fff,
    -1px -1px #fff,
    -1px 1px #fff,
    -5px 5px 5px rgb(200,200,200),
    -5px 5px 5px rgb(100,100,100);
    border-radius: 15px;
    margin:auto;
}

.SkillTitle{
    display: block;
    color:#ffff;
    width:300px;
    margin:auto;
    border-bottom: 2px solid #ffff;
    text-align: center;
    text-shadow: 1px 1px #000,
    1px -1px #000,
    -1px -1px #000,
    -1px 1px #000,
    -2px 2px 5px rgb(150,150,150),
    -3px 3px 5px rgb(100,100,100);
    box-shadow: 0px 10px 5px -5px rgb(100,100,100);
}
.Skills{
    position: absolute;
    left:50px;
    text-align: center;
    padding:20px 10px 10px 10px;
    background-color:#AB46D2;
    text-align: center;
    margin:4rem 3rem 3rem 4rem;
    width:360px;
    height:280px;
    display: inline-block;
    border-radius:50px 100px 50px 100px ;
    box-shadow: 1px 1px #fff,
    1px -1px #fff,
    -1px -1px #fff,
    -1px 1px #fff,
    -5px 5px 5px rgb(200,200,200),
    -5px 5px 10px 5px rgb(100,100,100);
}
.leftSkills h4{
    display: block;
}
.rightSkills h4{
    display: block;
}

.bullseye{
    color:#AB46D2;
    position: relative;
    left:40%;
    top:50px;
    animation: rotate 6s linear infinite;
}

.bullseye:hover{
    animation: rotate 1s linear infinite;
}

.leftSkills,.rightSkills{
    display: inline-block;
    position: relative;
    width:110px;
    margin:1rem 1.8rem;
}

.mySkill,.inte{
    font-size: 1.5rem;
    position: relative;
    color:#ffffff;
    text-shadow: 1px 1px #000,
    1px -1px #000,
    -1px -1px #000,
    -1px 1px #000,
    -3px 3px 5px rgb(100,100,100);
    margin:0.5rem;
    letter-spacing: 2px;
}

.mySkill:before{
    content: "oi1o";
    font-size: 10px;
    left:-20px;
    top:10px;
    letter-spacing: 0;
    position: absolute;
    overflow: hidden;
    border-radius: 50%;
    margin-right: 5px;
    text-shadow: none;
    box-shadow:1px 1px #000,
    1px -1px #000,
    -1px -1px #000,
    -1px 1px #000,
    -3px 3px 5px rgb(100,100,100);
    color:#ffff;
    background-color: #ffff;
}

.inte:before{
    content: "oi1o";
    font-size: 10px;
    left:25px;
    top:10px;
    letter-spacing: 0;
    position: absolute;
    overflow: hidden;
    border-radius: 50%;
    margin-right: 5px;
    text-shadow: none;
    box-shadow: 1px 1px #000,
    1px -1px #000,
    -1px -1px #000,
    -1px 1px #000,
    -3px 3px 5px rgb(100,100,100);
    color:#ffff;
    background-color: #ffff;
}
.inteDiv{
    left:65%;
}

.inteTitle,.LangTitle{
    margin-bottom: 1.5rem;
    padding-bottom: 5px;
}
.LangDiv{
    position: relative;
    top:50px;
    left:35%;
    display: block;
    border-radius: 40px 80px 40px;
    height:200px;
}