*{
  margin:0;
  font-family: 'Lobster', cursive;
}
.App{
  min-width: 700px;
  width:100vw;
  height:780px;
 
  background:radial-gradient(circle at 75% 55%,#AFB4FF 20%,#9C9EFE 20%,#A66CFF 30%,#A760FF 30%,#AB46D2);
}
.image{
  position: absolute;
  right:9.7%;
  top:10rem;
  -webkit-box-shadow: 0 8px 6px -6px black;
	   -moz-box-shadow: 0 8px 6px -6px black;
	        box-shadow: 0 8px 6px -6px black;
  
  min-height: 346px;
  min-width:300px;
  transform-origin: 225px 519px;
}

.imageDiv{
  position: relative;
}

.Card{
  color: #FEFBF6;
  padding-top: 3rem;
  position: relative;
  top:7%;
  left:5rem;
  text-align: center;
  width:650px;
  height:330px;
  border-radius: 15px;
  letter-spacing: 2px;
  line-height: 1.5;
}

.Card h1,.Card p,.Card h4{
  display:inline-block;
  font-size: 1.5rem;
  text-shadow: 1px 1px #000,
  1px -1px #000,
  -1px -1px #000,
  -1px 1px #000,
  -5px -5px 10px rgb(130,130,130),
  -5px 5px 10px rgb(100,100,100);
}

.Card h1{
  font-size: 4rem;
}

.Hand{
  height:20%;
  position: relative;
  right:3rem;
  top:0.6rem;
  transform-origin: 50px 100px;
  animation:wave alternate 250ms 6;
  animation-delay:1s;
}

@keyframes wave{
  0%{
    transform:rotate(-10);
  }
  50%{
    transform:rotate(0deg)
  }
  100%{
    transform:rotate(20deg);
  }
}

.TitleDiv{
  color:white;
  font-size: 1.5rem;
  text-shadow: -5px 0px 10px rgb(100,100,100);
  letter-spacing: 2px;
  display: inline-flex;
}

.compass{
  margin:0;
  position: absolute;
  top:-5px;
  left:17rem;
}

.TitleDiv h1:hover{
  text-shadow: 1px 1px #000,
  1px -1px #000,
  -1px -1px #000,
  -1px 1px #000,
  -2px 2px 15px white;
}

.TitleDiv h1{
  margin-top:1rem;
  letter-spacing: -2px;
  text-shadow: 1px 1px #000,
  1px -1px #000,
  -1px -1px #000,
  -1px 1px #000;
  border-bottom: 1px solid #fff;
  font-family: 'Montserrat', sans-serif;
}

.logo{
  width:50px;
  height: 50px;
  margin:1.2rem 2rem 0 0.7rem;
}

.navbar-brand{
  font-size: 200rem;
}

.IconsDiv{
  position: relative;
  margin:2.5rem 0 0.8rem 16rem;
}

.Sicons:hover{
  border-radius: 30%;
  color:#AB46D2;
  box-shadow:inset 0 0 10px 30px #fff,0 0 10px 5px #fff;
}

.Sicons{
  margin:5px
}

.cvbutton{
  text-decoration: none;
  color:#AB46D2;
  background-color: #fff;
  font-size: 3rem;
  padding:0.5rem 1rem;
  
  border-radius: 15px;
  position:relative;
  left:18rem;
}
.cvbutton:hover{
  background-color: #AB46D2;
  color:#fff;
  border:2px solid #fff;
  text-shadow: 0 0 10px #fff;
  box-shadow: 0 0 15px 15px #fff;
}

.svg{
  position: absolute;
  top:50px;
  left:0;
  transition: 2s;
}
.path{
  transition: 2s;
}

.download{
  margin:1rem 0 0 0.5rem;
  position: relative;
  top:0.6rem;
}
.aniimg{
  position: absolute;
  opacity:0.9;
}
.reactImg{
  right:350px;
  top:25px;
  height:150px;
  width:150px;
  animation:rotate 10s linear infinite,floatReact 2s linear infinite;
}
.nodeImg{
  right:150px;
  top:70px;
  height:150px;
  width:150px;
  animation: floatNode 2s linear 500ms infinite;
}
.javaImg{
  right:50px;
  top:230px;
  height:100px;
  width:100px;
  animation:floatJava 2s linear 200ms infinite;
}
.htmlImg{
  right:480px;
  top:270px;
  height:120px;
  width:120px;
  animation:floatHtml 2s linear infinite;
}
.cssImg{
  right:550px;
  top:120px;
  height:150px;
  width:150px;
  animation:floatCss 2s linear 150ms infinite;
}
.jsImg{
  right:630px;
  top:320px;
  height:150px;
  width:150px;
  animation: floatJs alternate 2s linear 50ms infinite;
}
.mdbImg{
  right:50px;
  top:400px;
  width:120px;
  height:120px;
  animation:floatMb 2s linear 250ms infinite;
}
.sqlImg{
  right:180px;
  top:320px;
  width:70px;
  height:60px;
  animation:floatSql 2s linear 600ms infinite;
}
.linuxImg{
  right:100px;
  top:530px;
  height:70px;
  width:70px;
  transform-origin: 35px 70px;
  animation:moveLinux 2s linear infinite;
}
.cppImg{
  right:550px;
  top:420px;
  height:80px;
  width:80px;
  animation: floatCpp 2s linear infinite;
}
.gitImg{
  right:580px;
  top:550px;
  height:60px;
  width:60px;
  animation:rotate 10s linear infinite,floatGit 2s linear 500ms infinite;
}

@keyframes rotate {
  from{
    transform:rotate(0deg);
  }
  to{
    transform:rotate(360deg);
  }
}

@keyframes floatReact {
  0%{
    top:20px;
  }
  50%{
    top:30px;
  }
  100%{
    top:20px;
  }
}
@keyframes floatNode {
  0%{
    top:70px;
  }
  50%{
    top:80px;
  }
  100%{
    top:70px;
  }
}
@keyframes floatJava {
  0%{
    top:230px;
    right:45px;
  }
  50%{
    top:240px;
    right:50px;
  }
  100%{
    top:230px;
    right:45px;
  }
}
@keyframes floatHtml {
  0%{
    top:250px;
    right:470px;
  }
  50%{
    top:260px;
    right:465px;
  }
  100%{
    top:250px;
    right:470px;
  }
}
@keyframes floatCss {
  0%{
    top:120px;
  }
  50%{
    top:130px;
  }
  100%{
    top:120px;
  }
}
@keyframes floatJs {
  0%{
    top:320px;
    transform:rotate(-10deg);
  }
  45%{
    top:326px;
    transform:rotate(5deg);
  }
  65%{
    top:330px;
    transform:rotate(6deg);
  }
  100%{
    top:320px;
    transform:rotate(-10deg);
  }
}
@keyframes floatCpp {
  0%{
    top:420px;
  }
  50%{
    top:430px;
  }
  100%{
    top:420px;
  }
}
@keyframes floatMb {
  0%{
    top:400px;
  }
  50%{
    top:410px;
  }
  100%{
    top:400px;
  }
}
@keyframes floatSql {
  0%{
    top:320px;
  }
  50%{
    top:330px;
  }
  100%{
    top:320px;
  }
}
@keyframes floatLinux {
  0%{
    top:400px;
  }
  50%{
    top:405px;
  }
  100%{
    top:400px;
  }
}
@keyframes floatGit {
  0%{
    top:550px;
  }
  50%{
    top:560px;
  }
  100%{
    top:550px;
  }
}
@keyframes moveLinux {
  0%{
    transform: rotate(-10deg);
  }
  25%{
    transform: rotate(0deg);
  }
  50%{
    transform: rotate(10deg);
  }
  75%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(-10deg);
  }
}

@media (max-width:1000px) {
  .App{
    height:1500px;
    background:radial-gradient(circle at 50% 25%,#AFB4FF 20%,#9C9EFE 20%,#A66CFF 40%,#A760FF 40% 95%,#AB46D2 98.2% 100%);
  }
  .image{
    position: relative;
    height: 346px;
    width: 300px;
    left:30%;
  }
  .Card{
    position: relative;
    margin:150px 0;
    display: block;
  }

}