*{
    margin:0;
}

.Projects{
    width:100vw;
    height:900px;
    padding-top: 80px;
    background-color: #fff;
    background-image: url("https://www.transparenttextures.com/patterns/asfalt-dark.png");
    position: relative;
    border-radius: 4rem;
    box-shadow: inset 0 0 5px rgb(100,100,100);
    display: inline-block;
}
.pl{
    position: relative;
    top:10px;
}

.prodiv{
    width:100vw;
    height:600px;
    position: relative;
    right:90px;
    top:10px;
    display: flex;
    flex-wrap: wrap;
}

.cardDiv{
    display: flex;
    flex:33.3333%;
    max-width:800px;
    min-width:500px;
    height: auto;
    text-align: center;
    position: relative;
}

.pro,.bpro{
    margin:120px 100px 0 0;
    height:250px;
    max-width: 300px;
    min-width:300px;
    cursor: pointer;
    backface-visibility: hidden;
    border-radius: 15px;
    box-shadow: 1px 1px #fff,
    1px -1px #fff,
    -1px -1px #fff,
    -1px 1px #fff,
    -5px 0px 5px rgb(200,200,200),
    -5px 5px 5px rgb(100,100,100);
    border:1px solid #AB46D2;
}
.bpro{
    margin:120px 0 0 0;
    background-color: #fff;
    position: absolute;
    transform:rotateY(180deg);
    left:40%;
}
.pro{
    position: relative;
    left:40%;
    background-size:300px 250px;
}

.visitbtn{
    position: absolute;
    border:none;
    font-size: 1.5rem;
    letter-spacing: 1px;
    width:100px;
    text-decoration: none;
    bottom:5%;
    left:100px;
    background-color: #AB46D2;
    color:#fff;
    border-radius: 10px;
    box-shadow: 1px 1px #fff,
    1px -1px #fff,
    -1px -1px #fff,
    -1px 1px #fff,
    -5px 0px 10px rgb(200,200,200),
    -5px 5px 10px rgb(100,100,100);
}

.appName{
    position: relative;
    bottom:50px;
    color:rgb(40,40,40);
    text-shadow: 1px 1px #fff,
    1px -1px #fff,
    -1px -1px #fff,
    -1px 1px #fff,
    -2px 0px 5px rgb(200,200,200),
    -2px 2px 5px rgb(100,100,100);
}

.aboutApp{
    padding:0 20px;
    color:#000;
    font-weight: 100;
    font-size: 1.1rem;
    letter-spacing: 1px;
    
}
.arrow{
    transform: rotate(45deg);
    color:#AB46D2;
}